@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color:  rgba(219, 234, 254); 
}
.App{
  background-color: rgba(191, 219, 254);
  min-height:500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 0.5rem;
}
.to-do-form h3{
font-family: 'Itim', cursive;
padding-top: 2rem;
margin-left: 7rem; 
color: lightcoral;
font-size: 35px;
}
.to-do-form p{
 font-weight: 900;
 text-align: center;
 color:lightcoral;
 font-size: 20px;
 font-family: 'Itim', cursive;
}
.to-do-form input{
  font-family: 'Itim', cursive;
  width: 250px;
  height: 50px;
  margin-top: 1rem;
  margin-left: 3rem;
  border-radius: 0.5rem;
  background-color:rgba(209, 213, 219);
  text-align: center;
  font-size: 18px;
}
.to-do-form button{
  font-family: 'Itim', cursive;
  margin:0.5rem;
  width: 65px;
  height: 55px;
  border-radius: 0.5rem;
  background-color: rgba(16, 185, 129);
  cursor: pointer;
  
}
.to-do-form input::-webkit-input-placeholder{
  font-family: 'Itim', cursive;
  color: black
}
.to-do-form input:-ms-input-placeholder{
  font-family: 'Itim', cursive;
  color: black
}
.to-do-form input::placeholder{
  font-family: 'Itim', cursive;
  color: black
}
  .to-do-form input:focus{
    outline: none;
  }
  .to-do-form buttont:focus{
    outline: none;
  }
  
.list p{
    font-family: 'Itim', cursive;
    color: #000;
   background-color: wheat;
   height: 40px;
   background-color: rgba(196, 181, 253);
   border-radius: 5px;
   margin-left: 3rem;
   padding: 10px;
   position: relative;
   transition: ease-in-out;
   font-size: 20px;
   width: 240px;
   text-align: left;
  }
 .list p button{
    width: 50px;
    height: 30px;
    float: right;
    border-radius: 0.5rem;
    font-family: 'Itim', cursive;
    background-color:  rgba(248, 113, 113);
    cursor: pointer;
 }

.del button{
  text-align: center;
  font-family: 'Itim', cursive;
  margin-left: 8rem;
  width: 100px;
  height: 50px;
  font-size: 20px;
  border-radius: 0.5rem;
  background-color:  rgba(248, 113, 113);
  cursor: pointer;
}
